import React from 'react'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'
import { connect, useSelector } from 'react-redux'
import Moment from 'react-moment'
import { removeComment } from '../../actions/post'

const CommentItem = ({
    postId,
    comment: { _id, text, name, avatar, user, date },
    removeComment
}) => {

    const auth = useSelector(state => state.rootReducer.auth)
    return (
        <div className="post bg-white p-1 my-1">
            <div>
                <Link to={`/profile/${user}`}>
                    <img className="round-img" src={avatar} alt="" />
                    <h4>{name}</h4>
                </Link>
            </div>
            <div>
                <p className="my-1">{text}</p>
                <p className="post-date">Posted on <Moment format='YYYY/MM/DD'>{date}</Moment></p>
                {!auth.loading && user === auth.user._id && (
                    <button
                        onClick={() => removeComment(postId, _id)}
                        type="button"
                        className="btn btn-danger"
                    >
                        <i className="fas fa-times" />
                    </button>
                )}
            </div>
        </div>
    )
}

CommentItem.propTypes = {
    postId: PropTypes.string.isRequired,
    comment: PropTypes.object.isRequired
}


export default connect(null, { removeComment })(CommentItem)